@media screen {
  .print-header {
    display: none !important;
  }
  .print-gap {
    display: none !important;
  }
  .only-print {
    display: none !important;
  }
  .print-header-margin {
    display: none !important;
  }
  .print-answer-mode {
    display: none !important;
  }
  .quiz-print-body {
    display: none !important;
  }
  .print-footer {
    display: none !important;
  }
}

@media print {
  .print-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    min-width: 0;
    background: linear-gradient(90deg, #2d3a60 0%, #6a5acd 100%);
    padding: 0.8rem 1.4rem 0.8rem 1.4rem;
    margin: 1cm 0 1.2rem 0;
    box-sizing: border-box;
    box-shadow: 0 4px 24px rgba(44,62,80,0.25);
    position: relative;
    z-index: 9999;
  }
  .print-header-margin {
    display: block;
  }
  .only-print {
    display: block;
  }
  .print-answer-mode {
    display: block;
  }
  .quiz-print-body {
    display: block;
  }
  .print-footer {
    display: block;
  }
  .print-header-logo {
    width: 42px;
    height: 42px;
    object-fit: contain;
    margin-right: 1.5rem;
  }
  .print-header-texts {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0.7rem;
  }
  .print-header-title {
    font-size: 1rem;
    font-weight: 800;
    color: #fff;
    letter-spacing: -1px;
    margin-right: 0.7rem;
    font-family: 'Noto Sans KR', 'IBM Plex Sans KR', Arial, sans-serif;
  }
  .print-header-tagline {
    font-size: 1rem;
    color: #e0e6f7;
    font-weight: 400;
    font-family: 'Noto Sans KR', 'IBM Plex Sans KR', Arial, sans-serif;
    border-left: 2px solid rgba(255,255,255,0.3);
    padding-left: 0.7rem;
  }
  .print-gap {
    display: block;
    height: 0;
    margin: 0;
    padding: 0;
    border: none;
    background: none;
  }
  .print-header + * {
    margin-top: 0;
  }
  .App,
  .App-main {
    padding-top: 0;
    margin-top: 0;
  }
  .no-print {
    display: none;
  }
  .only-print {
    display: block;
  }
  
  /*    /   */
  @page {
    margin: 0;
    size: A4;
  }
  
  /*       */
  .App, .App-main, .main-content, .work-container {
    margin: 0;
    padding: 0;
  }
  
  /*     /  */
  html, body {
    -webkit-print-color-adjust: exact;
    print-color-adjust: exact;
    margin: 0;
    padding: 0;
  }
  
  /*   , , URL,     */
  @media print {
    html, body {
      -webkit-print-color-adjust: exact;
      print-color-adjust: exact;
    }
    
    /*    /   */
    @page {
      margin: 0;
      size: A4;
    }
    
    /*      */
    * {
      -webkit-print-color-adjust: exact;
      print-color-adjust: exact;
    }
  }
  
  /* #02     */
  .print-header-package02 {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: #fff;
    padding: 0.3rem 0;
    margin: 0;
    box-sizing: border-box;
    border-bottom: none;
  }
  
  /*     */
  @media screen {
    .print-header-package02 {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      background: #fff;
      padding: 0.3rem 0;
      margin: 0;
      box-sizing: border-box;
      border-bottom: none;
    }
  }
  
  .print-header-text-package02 {
    font-size: 1rem;
    font-weight: 600;
    color: #000;
    font-family: 'Noto Sans KR', 'IBM Plex Sans KR', Arial, sans-serif;
  }
  
  /*      */
  @media screen {
    .print-header-text-package02 {
      font-size: 1rem;
      font-weight: 600;
      color: #000;
      font-family: 'Noto Sans KR', 'IBM Plex Sans KR', Arial, sans-serif;
    }
  }
  
  /* #02     - !important    */
  .only-print .problem-instruction {
    font-size: 1rem !important;
    box-shadow: none !important;
    padding: 0.7rem 0.6rem !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
    margin-top: 0 !important;
    margin-bottom: 0.8rem !important; /*   0.8rem  */
  }
  
  .only-print .problem-passage {
    font-size: 1rem !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
    margin-top: 0 !important;
    margin-bottom: 0.8rem !important;
    padding-left: 1rem !important;
    padding-right: 1rem !important;
    padding-top: 0.5rem !important;
    padding-bottom: 0.3rem !important;
  }

  /* #01-#02 :        */
  .only-print.print-answer-mode .problem-passage {
    margin-bottom: 1.6rem !important;
  }

  /* #01-#02 :       */
  .only-print.print-answer-mode .replacements-table {
    margin-top: 0rem !important;
  }

  
  .only-print .problem-options {
    font-size: 1rem !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
    padding-left: 0.6rem !important;
    padding-right: 0.6rem !important;
  }
  
  .only-print .shuffled-paragraph {
    font-size: 1rem !important;
  }
  
  .only-print .option {
    font-size: 1rem !important;
  }
  
  .only-print .answer-section {
    font-size: 1rem !important;
  }
  
  /*     -    */
  .only-print .paragraph-simple {
    font-size: 1rem !important;
  }
  
  .only-print .paragraph-simple div {
    font-size: 1rem !important;
  }
  
  .only-print .paragraph-simple strong {
    font-size: 1rem !important;
  }
  
  /* #02     -  1rem  */
  .only-print .vocabulary-generator,
  .only-print .vocabulary-generator *,
  .only-print .vocabulary-quiz-container,
  .only-print .vocabulary-quiz-container * {
    font-size: 1rem !important;
  }
  
  .only-print .quiz-content,
  .only-print .quiz-content * {
    font-size: 1rem !important;
  }
  
  .only-print .a4-page-template,
  .only-print .a4-page-template * {
    font-size: 1rem !important;
  }
  
  .only-print .a4-page-content,
  .only-print .a4-page-content * {
    font-size: 1rem !important;
  }

  /*    */
  .only-print .translation-content {
    font-size: 1rem !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
    padding-left: 1rem !important;
    padding-right: 1rem !important;
    padding-top: 0.5rem !important;
    padding-bottom: 0.3rem !important;
  }
  
  /* A4    */
  .a4-page-template {
    width: 21cm;
    height: 29.7cm;
    margin: 0;
    padding: 0;
    background: white;
    box-sizing: border-box;
    page-break-inside: avoid;
    position: relative;
    display: flex;
    flex-direction: column;
    left: 0;
    top: 0;
    /*    */
    --print-margin-horizontal: 0.8cm;
  }
  
  /*     -      */
  .only-print .a4-page-template {
    page-break-after: auto;
    page-break-before: auto;
    page-break-inside: avoid;
  }
  
  .a4-page-header {
    width: 100%;
    height: 1.5cm;
    flex-shrink: 0;
    padding: 0.5cm var(--print-margin-horizontal, 0.3cm) 0 var(--print-margin-horizontal, 0.3cm);
    box-sizing: border-box;
  }
  
  .a4-page-content {
    width: 100%;
    flex: 1;
    padding: 0.35cm var(--print-margin-horizontal, 0.3cm) 1cm var(--print-margin-horizontal, 0.3cm);
    box-sizing: border-box;
    overflow: visible;
    page-break-inside: auto;
    break-inside: auto;
    -webkit-column-break-inside: auto;
    height: auto;
    min-height: auto;
  }
  
  /*       */
  .only-print .problem-passage,
  .only-print .problem-options {
    page-break-inside: auto;
  }
  
  /* 4     */
  .only-print .problem-options {
    page-break-before: auto;
    page-break-after: auto;
  }
  
  .only-print .shuffled-paragraph {
    page-break-inside: avoid !important;
    page-break-after: auto !important;
    margin-bottom: 0.5rem !important;
  }
  
  .only-print .option {
    page-break-inside: avoid !important;
    margin-bottom: 0.3rem !important;
  }
  
  /*       -      */
  .only-print .a4-page-template:not(:first-child):not(:last-child) {
    page-break-after: always !important;
    break-after: page !important;
  }
  
  .only-print .a4-page-template:last-child {
    page-break-after: avoid !important;
    break-after: avoid !important;
  }
  
  /*   -      */
  @page {
    size: A4;
    margin: 0;
    padding: 0;
  }
  
  /*      */
  @page :first {
    margin: 0;
    padding: 0;
  }
  
  @page :left {
    margin: 0;
    padding: 0;
  }
  
  @page :right {
    margin: 0;
    padding: 0;
  }
  
  /* A4        */
  @page {
    @top-center {
      content: "";
    }
    @bottom-center {
      content: "";
    }
  }
  
  /*     */
  @media print {
    html, body {
      height: auto;
      overflow: visible;
      margin: 0;
      padding: 0;
      width: 100%;
    }
    
    .only-print {
      height: auto;
      overflow: visible;
      margin: 0;
      padding: 0;
      width: 100%;
      position: relative;
      left: 0;
      right: 0;
      top: 0;
    }
    
    /*        */
    .a4-page-template {
      break-inside: auto;
      -webkit-region-break-inside: auto;
      margin: 0;
      padding: 0;
      position: relative;
      left: 0;
      right: 0;
      top: 0;
    }
    
    .a4-page-content {
      break-inside: auto;
      -webkit-region-break-inside: auto;
    }
  }
  
  /* A4    */
  .a4-page-renderer {
    width: 100%;
    margin: 0;
    padding: 0;
  }
  
  /*    */
  @media screen {
    .a4-page-template {
      display: none !important;
    }
    .a4-page-renderer {
      display: none !important;
    }
    .a4-multi-page-container {
      display: none !important;
    }
  }
  
  /*       */
  .a4-multi-page-container {
    width: 100%;
  }
  
  /*    -     */
  .a4-page-template .a4-page-header {
    position: running(page-header);
  }
  
  /*        */
  @media print {
    .a4-page-content {
      page-break-inside: auto;
    }
    
    /*       */
    .problem-instruction {
      page-break-after: avoid;
      page-break-inside: avoid;
    }
    
    .problem-passage {
      page-break-before: auto;
      page-break-after: auto;
      page-break-inside: auto;
    }
    
    .shuffled-paragraph {
      page-break-inside: avoid;
      margin-bottom: 0.8rem;
      orphans: 2;
      widows: 2;
    }
    
    .problem-options {
      page-break-before: auto;
      page-break-inside: auto;
    }
    
    .option {
      page-break-inside: avoid;
      margin-bottom: 0.5rem;
    }
    
    /*   */
    .answer-section {
      page-break-before: avoid;
      page-break-inside: avoid;
    }
    
    /*    */
    .a4-page-template:empty {
      display: none;
    }
    
    /* A4    */
    .only-print .a4-page-template {
      page-break-inside: avoid;
      break-inside: avoid;
      -webkit-column-break-inside: avoid;
    }
    
    /* A4     */
    .only-print .a4-page-content {
      page-break-inside: auto;
      break-inside: auto;
      -webkit-column-break-inside: auto;
    }
    
    /* 4     */
    .only-print .problem-options {
      page-break-inside: avoid;
      break-inside: avoid;
    }

     /* #02-#02     -    */
     .replacements-table {
       width: 100%;
       border-collapse: collapse;
       margin: 1rem 0;
       font-size: 1rem !important;
       table-layout: fixed;
     }

     .replacements-table th,
     .replacements-table td {
       border: 1px solid #000;
       padding: 0.2rem 0.3rem;
       text-align: left;
       vertical-align: top;
       line-height: 1.3 !important;
       width: 25%;
     }

     .replacements-table th {
       background: #f5f5f5;
       font-weight: bold;
       text-align: center;
     }

     .replacements-table td {
       background: #fff;
     }

     .replacements-table .original-word,
     .replacements-table .replacement-word {
       font-weight: normal;
       color: #000;
       background: none;
       padding: 0;
       margin: 0;
       font-size: 0.8rem !important;
       line-height: 1.2 !important;
     }

     .replacements-table .original-meaning,
     .replacements-table .replacement-meaning {
       color: #666;
       font-style: italic;
       background: none;
       padding: 0;
       margin: 0;
       font-size: 0.7rem !important;
       line-height: 1.2 !important;
     }

     /*  #02    */
     .only-print .replacements-table th,
     .only-print .replacements-table td {
       font-size: 1rem !important;
     }
  }

  /* #01-#02, #03, #04, #05, #06, #09, #10 () 2     */
  .only-print .problem-passage.translation {
    background: #F1F8E9 !important;
    -webkit-print-color-adjust: exact !important;
    print-color-adjust: exact !important;
  }
  
  /* #09, #10      */
  .only-print .translation-section {
    page-break-before: auto;
    page-break-inside: avoid;
  }
  
  /* #10   */
  .only-print.work-10-print .a4-page-template {
    page-break-before: auto;
    page-break-inside: avoid;
  }
  
  .only-print.work-10-print .a4-page-header {
    display: block !important;
  }
  
  .only-print .problem-passage:not(.translation) {
    background: #FFF3CD !important;
    -webkit-print-color-adjust: exact !important;
    print-color-adjust: exact !important;
  }
}

